import React from 'react'
export default  class Hot extends React.Component{
    componentDidMount () {  
    this.onLoading()
    }
    onLoading () {
        fetch('http://localhost:8084/movie/hot/?city=bj')
        .then(res=> res)
        .then(res=>{
            console.log(res)
        }) 
    }
render (){
    return (<div>
        <h1>电影1</h1>
    </div>)
}
} 


